import React from 'react';
import './AsideNav.styl';
import 'font-awesome/css/font-awesome.min.css';
import { Link } from 'react-router';

const AsideNav = () => {
    return (
    	<aside className="aside">
			<div className="aside--list-groups">
				<input type="checkbox" aria-hidden="true" id="for-private"/>
				<label htmlFor="for-private" aria-hidden="true" className="aside-nav--item">   </label>
				<ul>
					<li><a href="#">    </a></li>
					<li><a href="#"> </a></li>
					<li><a href="#"> </a></li>
					<li><a href="#"></a></li>
					<li><a href="#"></a></li>
				</ul>

				<input type="checkbox" aria-hidden="true" id="for-business"/>
				<label htmlFor="for-business" aria-hidden="true" className="aside-nav--item">  </label>
				<ul>
					<li><a href="#"> 1</a></li>
					<li><a href="#"> 2</a></li>
					<li><a href="#"> 3</a></li>
					<li><a href="#"> 4</a></li>
					<li><a href="#"> 5</a></li>
				</ul>
			</div>

			<div className="aside--item">
				<Link to="-"> </Link>
			</div>
			<div className="aside--item">
				<Link to=""></Link>
			</div>

		</aside>
	);
};

export default AsideNav;


